body {
  background-color: #eee;
  width: 100%;
  height: 100%;
  font-family: Montserrat;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li, p, h1, h2 {
  margin: 0;
  padding: 0;
}

input {
  border: none;
  outline: none;
}

.backdrop {
  position: fixed;
  inset: 0;
}

.evening-theme {
  background: linear-gradient(#e66465, #9198e5);
}

.day-theme {
  background: linear-gradient(to right, #c6ffdd, #fbd786, #f7797d);
}

.night-theme {
  background: linear-gradient(to right, #16222a, #3a6073);
}

.city-input {
  z-index: 2;
  color: #333;
  border-radius: 10px;
  width: 15vw;
  height: 5vh;
  padding: 10px;
  font-size: 1vw;
  font-weight: 400;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.forecast {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1.9vw;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.forecast__item {
  color: #eee;
  background: linear-gradient(90deg, #5936b4 0%, #362a84 100%);
  border-radius: 10px;
  width: 10vw;
  height: 15vh;
  padding: .8vw;
}

.forecast__text {
  text-wrap: balance;
  margin-bottom: .25vw;
  font-size: .9vw;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

